@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#brand {
  font-family: 'Lobster', cursive;
}

.header-icons {
  font-size: 35px;
  float: right;
}

.footer-icons {
  font-size: 20px;
  top: 10px;
  grid-row: 1 / span 5;
}

.bi {
  padding: 5px;
  margin: 10px;
  top: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.storyImg {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  object-fit: cover;
  /* border-width: 3px;
  border-style: solid;
  border-color: red; */
  padding: 4px;
  background: linear-gradient(rgb(255, 0, 0), rgb(255, 0, 170)) padding-box,
  linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255)) border-box;
 }
  
.storyList {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: nowrap;
  /* overflow-x: scroll; */
}

.story {
  flex-direction: column;
}

.postImg {
  margin: auto;
  width: 50vw;
}

.user-photo {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  padding: 2px;
  background: linear-gradient(rgb(255, 0, 0), rgb(255, 0, 170)) padding-box,
  linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255)) border-box;
  margin-right: 10px;
}

.post-heading {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.post-options {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  margin: 5px 0 5px 0;

}

.post-option {
  font-size: 27px;
  margin-left: 0;
}

.app {
  width: 50vw;
  margin: auto;
  padding-top: 0;
  margin-top: 0;
}


.story-text {
  text-align: center;
  margin-top: 5px;
  margin-bottom: 5px;
}


.header {
  position: fixed;
  background-color: white;
  width: 50vw;
  top: 0;
  border-bottom: 1px solid rgb(100, 100, 100);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.footer {
  position: fixed;
  bottom: 0px;
  background-color: white;
  width: 50vw;
  border-top: 1px solid rgb(100, 100, 100);

}

.footer-icons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

body {
  padding-top: 100px;
  padding-bottom: 80px;
}

.date {
  font-size: .8rem;
  color:rgb(100, 100, 100);
  margin-top: 0;
}

.post-info {
  line-height: 1.6rem;
}

.comment {
  display: block;
  line-height: 1.4rem;
}

.view-all-comments {
  color: rgb(100, 100, 100);
  line-height: 1.6rem;
}